// Header
header {
  @include flex(flex-start, center, column);
  background: $white;
  width: 100%;
  height: 9.5rem;

  @media screen and (max-width: 1080px) {
    height: 10rem;
    background: $lightPurple;
    position: fixed;
    z-index: 2;
  }

  .row {
    @include flex(space-between);
    padding: 1rem 0;

    @media screen and (max-width: 1080px) {
      justify-content: flex-start;
      padding: 0;
      // width: 100%;
    }

    img.logo {
      @media screen and (max-width: 1080px) {
        margin-top: 2rem;
        width: 20rem;
      }
    }


    address {
      @include flex(space-between);
      width: 9rem;

      @media screen and (max-width: 1080px) {
        display: none;
      }

      a {
        display: block;
        width: 2rem;
        height: 2rem;
      }

      a.eMail {
        background: url(../images/icons/eMail.svg) no-repeat;
        background-size: contain;
      }
      a.location {
        background: url(../images/icons/location.svg) no-repeat;
        background-size: contain;
      }
      a.telegram {
        background: url(../images/icons/telegram.svg) no-repeat;
        background-size: contain;
      }
    }
  }
  .wrapper {
    width: 100%;
    background-color: $lightPurple;
    @include flex();

    z-index: 999;

    @media screen and (max-width: 1080px) {
      background: none;
    }

    nav {
      @include flex(space-between);
      @extend .row;
      padding: .5rem 0;
      @media screen and (max-width: 1080px) {
        justify-content: flex-end;
        padding: 0;
      }

      .btn-menu {
        display: none;
      }

      .hooks {
        @include flex(space-between);
        width: 44rem;

        @media screen and (max-width: 1080px) {
          @include flex(flex-start, center, column);
        }

        a {
          color: $darkPurple;
          font-weight: $regular;
          font-size: $smallSize;
        }

        .social {
        }

      }

      a.btn {
        @media screen and (max-width: 1080px) {
          display: none;
        }
      }
    }
  }
}
