section#clients {
  @include flex();
  margin-top: 20rem;

  @media screen and (max-width: 1080px) {
    margin-top: 25rem;
  }

  .wrapper {
    @include flex(flex-start, flex-start, column);
    @extend .row;

    h3 {

    }

    .row {
      @include flex(space-between);
      width: 55rem;
      //чтобы можно было добавлять еще клиентов:
      // - если добавить 1 клиента (width: 84rem), если добавить 2 и более (в сумме 4+):
      // width: 100%; flex: 1; flex-wrap: wrap;
      // ну а с картинкой хз - чо захотите :)
      @media screen and (max-width: 1080px) {
        width: 100%;
      }
      img {
        width: 26rem;
        transition: .5s ease;

        @media screen and (max-width: 1080px) {
          width: 48%;
        }
      }

    }

  }
  .background4 {
    position: absolute;
    width: 50rem;
    transform: translate(20rem, 20rem);
    z-index: -2;

    @media screen and (max-width: 1080px) {
      display: none;
    }
  }
}
