//In header
.navFixed {
  position: fixed;
}
.mainFixed {
  position: fixed;
  top: 4.5rem;
}
//In Main
//In whatWeDo
.offset { //for main after animation
  position: absolute;
  top: 800vh;
}

.circleActive {
  background: $darkPurple !important;
}

//for text in animationZone
.frameStay {
  transform: translateX(-5rem);
  opacity: 0;
}

.frameIn {
  transform: translateX(0) !important;
  opacity: 1 !important;
}

.frameOut {
  transform: translateX(5rem) !important;
  opacity: 0 !important;
}
//animatedImages -> thirdAnimImage
.flow {
  animation: 5s ease-in-out upToDown infinite forwards;
}

//text animation
.unvisible {
  opacity: 0 !important;
  transform: translateX(-6rem) !important;
}

//for mobile
//menu

.show {
  visibility: visible !important;
  opacity: 1 !important;
}

.burgerRotate .a {
   transform: rotate(-45deg) translate(-1rem, .7rem);
}

.burgerRotate .c {
  transform: rotate(45deg) translate(-1rem, -.7rem);
}

.burgerRotate .b {
  opacity: 0;
}

.burgerRotate {
  transform: rotate(180deg);
}
