html {
  font-size: 0.833vw;
  background-color: $background;
  font-family: 'Roboto', sans-serif;
  font-weight: $light;

  @media screen and (max-width: 1080px) {
    font-size: 2vw;
  }
}

body {
  scroll-behavior: smooth;
}

button:active, button:focus, input, input:focus, input:active {
  outline: none;
  border: none;
  font-family: inherit;
}

a {
  text-decoration: none;
  transition: .5s ease;
}

// fonts
h1,h2,h3,h4,h5,h6,p {
  margin: 0;
  padding: 0;
  transition: .5s ease;
}

h1,h2 {
  font-weight: $bold;
  font-size: $hugeSize;
  color: $white;
  margin: 2rem 0;
}

h3 {
  font-weight: $medium;
  font-size: $mediumSize;
  color: $white;
  @media screen and (max-width: 1080px) {
    font-size: $bigSize;
    font-weight: $regular;
  }
}

h4 {
  font-weight: $medium;
  font-size: $bigSize;
  color: $white;
  @media screen and (max-width: 1080px) {
    font-size: $mediumSize;
    font-weight: $regular;
  }
}

p {
  font-weight: $light;
  font-size: $mediumSize;
  color: $purple;
  margin-top: 1rem;
}

// common styles
.row {
  width: $gridWidth;

  @media screen and (max-width: 1080px) {
    width: 85%;
  }
}

.logo {
  width: 12rem;
}

.btn {
  font-weight: $medium;
  font-size: $smallSize;
  padding: .8rem 3rem;
  background: $darkPurple;
  color:white;
  border-radius: $btnBorderRadius;
  box-shadow: $btnShadow;
  transition: .5s ease;


  &:hover {
    background: $purple;
  }
}

address {
  @include flex(space-between);

  a {
    display: block;
    width: 2rem;
    height: 2rem;
  }
}

.hooks, .navigation {
  a {
    &:hover, &:active {
      transform: translateY(-.15rem);
      color: $purple !important;
    }
  }
}
// for svg from animaiton zone
@media screen and (max-width: 1080px) {
  .relative {
    position: relative;
  }
}
