main {
  @include flex(center, flex-start);
  width: 100%;
  $mheight: 100vh;
  height: $mheight;
  background: url(../../images/bg.svg) no-repeat;
  background-size: cover;

  @media screen and (max-width: 1080px) {
    @include flex(flex-start, center, column);
    background: none;
    height: auto;
    overflow: visible;
    background-color: rgba(0,0,0,.1);
    padding-top: 10rem;
  }

  position: relative;
  overflow: hidden;

  z-index: 1;

  .wrapper {
    @extend .row;
    margin-top: 3.5rem;

    .card {
      @include flex(center, flex-start, column);

      h1 {}

      span {

        h2 {}

        p {
          width: 37rem;
        }
      }

      .btn {
        margin-top: 2rem;
      }
    }
  }

  .animationZone {
    position: absolute;
    height: $mheight;
    width: 50%;
    background: #3E1D8F;

    left:100%;

    transition: .5s ease;

    @media screen and (max-width: 1080px) {
      @include flex(flex-start, flex-start);

      position: relative;
      background: none;
      width: 85%;
      left: 0;
      height: auto;
      overflow: hidden;
      padding-bottom: 10rem;
    }

    .scrollIndicator {
      position: absolute;

      height: $mheight;
      width: .5rem;
      background: $white;

      top: 0;
      left: 0;

      @media screen and (max-width: 1080px) {
        display: none;
      }

      .indicator {
        position: absolute;
        top:0;
        left:0;

        width: 105%;
        height: 0;

        background: $darkPurple;

        transition: .3s ease;
      }
    }

    .circles {
      @include flex(space-between, center, column);
      height: 5rem;

      position: absolute;

      left:96%;
      top: 48%;
      transform: translateY(-50%);

      @media screen and (max-width: 1080px) {
        display: none;
      }

      span {
        width: 1rem;
        height: 1rem;
        border-radius:100%;
        background: $white;
      }
    }

    .frame {
      position: absolute;
      width: 100%;
      height: 100%;

      @media screen and (max-width: 1080px) {
        position: relative;
        height: auto;
      }

      span {
        position: absolute;
        top: 5.5rem;
        left: 5.5rem;
        @extend .frameStay;

        transition: .3s ease-in-out;

        @media screen and (max-width: 1080px) {
          position: relative;
          display: block;
          opacity: 1;
        }

        &.support {
          @media screen and (max-width: 1080px) {
            h4 {
              margin-top: 5rem;
              width: auto;
            }
          }
        }

        &.alerting24 {
          h4 {
              width: 33rem !important;

              @media screen and (max-width: 1080px) {
                margin-top: 5rem;
              }
            }

          p {
            width: 21rem;
          }
        }

        &.delivery {

          h4 {
            width: auto !important;

            @media screen and (max-width: 1080px) {
              margin-top: 5rem;
            }
          }
          p {
            width: 25rem;
          }
        }

        h4 {
          width: 19rem;
        }
      }

        .firstAnimImage {
          position: absolute;

          width: 100%;
          height: 67.5rem;

          left: 8rem;
          top: 0rem;

          @media screen and (max-width: 1080px) {
            top: -15rem;
            left: 10rem;
          }
        }

        .secondAnimImage {
          position: absolute;
          width: 100%;
          left: 8rem;
          top: 3rem;

          @media screen and (max-width: 1080px) {
            top: 0rem;
            left: 11rem;
          }
        }
        .thirdAnimImage {
          position: absolute;
          width: 100%;
          top: 0;
          left:3rem;

          @media screen and (max-width: 1080px) {
            top: -5rem;
            left: 12rem;
            display: none;
          }
        }
    }
  }
}

.space {
  width: 100%;
  height: 900vh;
  @media screen and (max-width: 1080px) {
    display: none;
  }
}

// .active {
