//The second part of this block (after animation)
section.whatWeDo {
  @include flex();
  margin-top: 2rem;
  width: 100%;
  z-index: 2;
  @media screen and (max-width: 1080px) {
    margin: 0;
    position: relative;
    background-color: rgba(0,0,0,.1);
    z-index: 0;
    padding-bottom: 10rem;

  }

  // position: relative;

  .wrapper {
    @include flex(center, center, column);

    .row {

      &.first{

        span {
          @include flex(flex-start, flex-start, column);
          @media screen and (max-width: 1080px) {
            margin-top: 5rem;
          }

          p {
            @media screen and (max-width: 1080px) {
              width: 100%;
            }
          }
        }
      }

      &.second {
        margin-top: 15rem;

        span {
          @include flex(flex-start, flex-end, column);

          @media screen and (max-width: 1080px) {
            margin-top: 15rem;
          }
        }

        p {
          @media screen and (max-width: 1080px) {
            width: 100%;
          }
        }
      }

      p,h3 {
        width: 30rem;

        @media screen and (max-width: 1080px) {
          width: 100%;
          text-align: left;
        }
      }
    }
  }
  object {
    position: absolute;
    width: 100%;
    height: 56rem;
    z-index: -1;

    @media screen and (max-width: 1080px) {
      top: 25rem;
      height: auto;
    }
  }
}
