footer {
  @include flex(center, center, column);
  margin-top: 15rem;
  background-color: $lightPurple;
  padding: 1rem 0;

  .navigation {
    @include flex(space-between);
    width: 44rem;

    a {
      font-size: $smallSize;
      font-weight: $regular;
      color: $darkPurple;
    }
  }

  address {
    @include flex(space-between);
    width: 6rem;

    margin-top: 1rem;

    a.eMail {
      background: url(../images/icons/eMail.svg) no-repeat;
      background-size: contain;
    }
    a.telegram {
      background: url(../images/icons/telegram.svg) no-repeat;
      background-size: contain;
    }
  }

  p {
    font-size: 1rem;
    font-weight: $light;
    color: $darkPurple;
  }
}
