section#contacts {
  @include flex();
  margin-top: 25rem;

  @media screen and (max-width: 1080px) {
    margin-top: 15rem;
  }

  .wrapper {
    @include flex(space-between);
    @extend .row;

    @media screen and (max-width: 1080px) {
      @include flex(space-between, center, column);
    }

    form {
      @include flex(flex-start, flex-start, column);
      width: 32.5rem;

      @media screen and (max-width: 1080px) {
        width: 100%;
      }


      legend {

        h4 {
          text-align: center;
          width: 32.5rem;

          @media screen and (max-width: 1080px) {
            width: 40rem;
          }
        }
      }

      input[type="name"], input[type="text"] {
        width: 100%;
        height: 4.5rem;
        background-color: none;
        border: 0.25rem solid $darkPurple;
        border-radius: 1.5rem;

        margin-top: 2rem;

        @media screen and (max-width: 1080px) {
          border-radius: 3rem;
          height: 5rem;
        }

        &[placeholder], &::placeholder {
          color: $purple;
          text-indent: 1rem;
          font-size: $smallSize;

          @media screen and (max-width: 1080px) {
            text-indent: 3rem;
          }
        }
      }

      input[type="submit"] {
        width: 100%;
        height: 4.5rem;
        background-color: $darkPurple;
        color: white;
        border-radius: 2rem;
        margin-top: 2.5rem;
        font-family: 'Roboto', sans-serif;
        font-weight: $medium;
        font-size: $smallSize;

        @media screen and (max-width: 1080px) {
          border-radius: 3rem;
          height: 5rem;
        }

        &:hover {
          cursor: pointer;
          background-color: $purple;
        }
      }
    }

    .information {
      @media screen and (max-width: 1080px) {
        margin-top: 5rem;
      }
      .social {
        @include flex(flex-start);
        margin-top: 2rem;

        a.eMail {
          background: url(../images/icons/eMail-white.svg) no-repeat;
          background-size: contain;
        }
        a.telegram {
          background: url(../images/icons/telegram-white.svg) no-repeat;
          background-size: contain;
          margin-left: 2rem;
        }
      }
    }
  }
}
