section#devOpsTool {
  @include flex();
  margin-top: 20rem;

  @media screen and (max-width: 1080px) {
    margin-top: 6rem;
  }

  .wrapper {
    @include flex(flex-start, flex-start, column);
    @extend .row;

    p {
      width: 54rem;
      @media screen and (max-width: 1080px) {
        width: 100%;
      }
    }

    .row {
      @include flex(space-between);
      margin-top: 8rem;
      width: 60rem;
      flex-wrap: wrap;
      flex: 1;

      @media screen and (max-width: 1080px) {
        width: 100%;
        margin-top: 4rem;
      }

      span {
        margin-top: 4rem;

        @media screen and (max-width: 1080px) {
          margin-top: 2rem;
        }


        h3 {
          width: 26rem;
          @media screen and (max-width: 1080px) {
            width: 20rem;
            font-size: $smallSize;
          }
        }

        p {
          width: 23rem;

          @media screen and (max-width: 1080px) {
            width: 20rem;

            font-size: $smallSize;
          }
        }
      }
    }
  }
  .background3 {
    width: 91rem;
    height: 100rem;
    position: absolute;
    display: block;
    margin: 0 auto;
    z-index: -1;

    @media screen and (max-width: 1080px) {
      width: 100%;
      top:240rem;
    }

  }
}
