@media screen and (max-width: 1080px) {
  .btn-menu {
    display: block !important;
    width: 5.875rem;
    height: 3.5rem;
    margin-top: -4rem;

    transition: .5s ease;


    hr {
      width: 100%;
      height: .7rem;
      background: $darkPurple;
      border-radius: 2rem;
      border: none;

      transition: .3s ease;

      &:nth-child(1) {
        margin-top: 0;
      }

    }
  }

  .hooks {
    opacity: 0;
    visibility: hidden;

    margin-top: 9.5rem;
    padding-top: 8vh;

    height: 100vh;
    width: 100vw !important;
    background: $lightPurple;
    position: fixed;
    top:0;
    left:0;

    transition: .5s ease;

    a {
      margin-bottom: 5vh;
      font-size: 5vh !important;
      font-weight: $regular !important;
    }

    .social {
      display: block !important;
      width: auto;


      a {
        display: inline-block;
        height: 6vh;
        width: 6vh;
        background-color: red;
        margin-top: 15%;
        margin-left: 2vh;

        &.eMail {
          background: url(../images/icons/eMail.svg) no-repeat;
          background-size: contain;
        }
        &.telegram {
          background: url(../images/icons/telegram.svg) no-repeat;
          background-size: contain;
        }
        &.location {
          background: url(../images/icons/location.svg) no-repeat;
          background-size: contain;
        }

      }
    }
  }
}
